<template>
	<div>
		<div class="header">
			<div class="logo">
				<img src="../assets/image/logo.png" alt="">
			</div>
			<div class="title">
				<span>图巴诺校园安全</span>
			</div>
			<!-- 折叠按钮 -->
			<div class="collapse-btn" @click="collapseChage">
				<i v-if="!collapse" class="el-icon-s-unfold icon"></i>
				<i v-else class="el-icon-s-fold icon"></i>
			</div>
			<!-- 个人中心 -->
			<div class="info">
				<el-dropdown @command="handleCommand">
					<span class="el-dropdown-link">
						{{username}}
						<i class="el-icon-caret-bottom"></i>
					</span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item divided command="logout">切换账号</el-dropdown-item>
						<el-dropdown-item divided command="resetPassword">修改密码</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>
			<div class="logout">
				<i class="el-icon-circle-close icon" @click="logout"></i>
			</div>
		</div>
		
		<div>
			<el-dialog title="修改密码" :visible.sync="dialogResetPassWord" width="500px" :close-on-click-modal="false">
				<div class="box">
					<el-form :model="pwdForm" label-width="120px">
						<el-form-item label="旧密码">
							<el-input v-model="pwdForm.old_password"></el-input>
						</el-form-item>
						<el-form-item label="新密码">
							<el-input v-model="pwdForm.new_password"></el-input>
						</el-form-item>
						<el-form-item label="再次确认新密码">
							<el-input v-model="pwdForm.confirm_password"></el-input>
						</el-form-item>
						<div class="submit">
							<el-form-item>
								<el-button type="primary" @click="ChangePassword">提交</el-button>
							</el-form-item>
						</div>
					</el-form>
				</div>
			</el-dialog>
		</div>
	</div>

</template>

<script>
	import bus from './bus'
	import API from '@/api/login.js'

	export default {
		data() {
			return {
				dialogResetPassWord: false,
				pwdForm: {
					new_password: '',
					old_password: '',
					confirm_password: ''
				},
				collapse: false,
				username: localStorage.getItem('username')
			}
		},
		methods: {
			collapseChage() {
				var self = this;
				self.collapse = !self.collapse;
				bus.$emit('collapse', this.collapse);
			},
			// 用户名下拉菜单选择事件
			handleCommand(command) {
				var self = this;
				if (command == 'logout') {
					API.logout().then(res => {
						localStorage.removeItem('username')
						localStorage.removeItem('token')
						localStorage.removeItem('role')
						window.location.reload()
						self.$router.replace('/login')
					})
				}
				if(command == 'resetPassword') {
					self.dialogResetPassWord = true;
				}
			},
			ChangePassword() {
				var self = this;
				if(self.pwdForm.new_password !== self.pwdForm.confirm_password) {
					self.$message.error('新密码与确认密码不一致');
				}
				if(self.pwdForm.new_password && self.pwdForm.old_password && self.pwdForm.confirm_password) {
					API.resetPassword(self.pwdForm).then(res => {
						self.pwdForm = {};
						self.$message.success('修改成功，请重新登录');
						localStorage.removeItem('username')
						localStorage.removeItem('token')
						localStorage.removeItem('role')
						window.location.reload()
						self.$router.replace('/login')
					})
				} else {
					self.$message.error('请完善信息');
				}
			},
			logout() {
				var self = this;
				API.logout().then(res => {
					localStorage.removeItem('username')
					localStorage.removeItem('token')
					localStorage.removeItem('role')
					window.location.reload()
					self.$router.replace('/login')
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.header {
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		height: 50px;
		font-size: 20px;
		background-color: #333;
		color: #fff;
	}

	.logo {
		position: absolute;
		top: 13px;
		left: 15px;
		width: 20px;
		height: 20px;
	}

	.logo img {
		width: 25px;
		height: 25px;
		border-radius: 4px;
	}

	.title {
		position: absolute;
		top: 15px;
		margin-left: 50px;
		font-size: 20px;
	}

	.collapse-btn {
		position: absolute;
		top: 15px;
		left: 210px;
		background: rgba(255, 255, 255, .1);

	}

	.collapse-btn .icon,
	.logout .icon {
		font-size: 20px;
		cursor: pointer;
	}

	.info {
		position: absolute;
		top: 15px;
		right: 80px;
		border-right: 1px #fff solid;
		padding-right: 10px;

		.el-dropdown-link {
			font-size: 20px;
			color: #fff;
		}
	}

	.logout {
		position: absolute;
		top: 15px;
		right: 50px;
	}
</style>
